import React from "react";
import { useStaticQuery, graphql } from "gatsby";

const BlocksDetails = () => {
  const data = useStaticQuery(graphql`
    {
      allBlocksJson {
        nodes {
          id
          block
          count
        }
      }
    }
  `);
  return (
    <div className="bg-brand-turquoise-100">
      <div className="container mx-auto">
        <div className="grid xl:grid-cols-2 lg:grid-cols-2 grid-cols-1 lg:col-gap-8 row-gap-12 py-20">
          <div className="flex flex-col">
            <h2 className="font-bold pb-6">
              Blocks you can use to design your website.
            </h2>
            <div>
              <a
                className="bg-brand-purple-900 text-white text-xl leading-6 font-medium py-4 px-5 rounded"
                href="/"
                target="_blank"
              >
                Get Landify
              </a>
            </div>
          </div>
          <div className="flex flex-col">
            <p className="text-lg pt-3 pb-8">
              Find what you need among <b>93 blocks</b>, divided into{" "}
              <b>13 categories</b> based on the purpose and use case.
            </p>
            <div className="grid md:grid-cols-2 grid-cols-1 col-gap-8">
              {data.allBlocksJson.nodes.map((node) => (
                <div
                  className="grid lg:grid-cols-4 grid-cols-2 pb-4"
                  key={node.id}
                >
                  <p className="lg:col-span-3 col-span-1">{node.block}</p>
                  <p className="lg:col-span-1 col-span-1">{node.count}</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BlocksDetails;
